.slick-slider-section {
  box-sizing: border-box;
  padding: 20px 30px 0;
  margin: 0; }
  .slick-slider-section .slick-list {
    margin: 0 20px; }
    .slick-slider-section .slick-list h3 {
      text-align: center; }
  .slick-slider-section .slick-arrow {
    filter: invert(100%); }
  .slick-slider-section .slick-dots {
    bottom: auto;
    position: relative; }
